import React, { useState } from 'react';
import './DetalieTop.css'; // CSS    
import 'bootstrap/dist/css/bootstrap.min.css';
import {Container, Card, Modal} from 'react-bootstrap';

function importAll(r) {
  return r.keys().map(r);
}

const bannerImages = importAll(require.context(`../images/banner`, false, /\.(png|jpe?g|gif|svg)$/));

const images = [...bannerImages];

const AddSome = () => {
   //   : /
    const [show, setShow] = useState(false);
    //    URL 
    const [selectedImage, setSelectedImage] = useState('');
  
    const handleClose = () => setShow(false);
    const handleShow = (imgSrc) => {
      setSelectedImage(imgSrc); //   URL 
      setShow(true);            //  
    };
  return (
    <>
    <Container className='mt-5'>
    <div className='text-color'>
        <div>
            <p>
                <span className='fs-4 fw-mute'> </span><br />
                <span className='fs-1 fw-bold'> </span>
            </p>
        </div>
        {/*  */}  
        <Card className='shadow border-0 rounded-2'>
      <div className="masonry-grid mx-3 my-3">
        {images.map((img, i) => (
            <div key={i} className="masonry-item">
                <div 
                  className="border-0 rounded-1 overflow-hidden"
                  onClick={() => handleShow(img)} //    
                  style={{ cursor: 'pointer' }} //    
                >
                    <img
                        src={img}
                        alt={` ${i + 1}`}
                        className="img-fluid d-block"
                        loading="lazy"
                    />
                </div>
            </div>
        ))}
      </div>
        </Card>
        {/* 2. Modal   */}
        <Modal show={show} onHide={handleClose} size="lg" centered>
        {/* Modal Body   . */}
        <Modal.Body className="p-0">
          <img 
            src={selectedImage} 
            alt=" " 
            className="img-fluid" 
            //       
            onClick={handleClose} 
            style={{ cursor: 'pointer' }}
          />
        </Modal.Body>
        </Modal>
      </div>
    </Container>
    </>
  )
}

export default AddSome
