import { useState } from 'react';
import { Link } from 'react-router-dom';
import {Container, Offcanvas, Button, Row, Col} from 'react-bootstrap';
import './MainCanvas.css';


function MainCanvas() {
  
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  
  return (
    <>
    <Container>
      {/*  */}
      <Row>
        <Col className='mt-5 ms-5 text-color'>
          <br/><br/><br/><br/><br/>
          <div className='fw-bold fade-up'>   
            <p className='fs-2'>  </p>
            <p className='fs-2'>
              <span className="highlight"> 5  </span> <br/>
              <span className="highlight"></span>.
            </p>
          </div>
          <div className='mt-5 fade-up delay'>
            <p className='fw-mute fs-small mx-0'>
                100% .
            </p>
          </div>
        </Col>       
      {/*  */}
        <Col className='text-center mt-5'>
          <div style={{ position: 'relative' }}>
        <Col Row className="justify-content-end" xs="auto">
        <br/>
          <Button className='btn-color floating-btn' onClick={handleShow}>
            
          </Button>
        </Col>
          <Offcanvas show={show} onHide={handleClose} placement="end">
            <Offcanvas.Header closeButton >
              <Offcanvas.Title className='mt-5 fs-3 ps-3 fw-bold' ><br /></Offcanvas.Title>
            </Offcanvas.Header>
            <Offcanvas.Body>
              <div className='fs-4 ps-3'>
                {/* <br />
                <div style={{ position: 'relative', display: 'inline-block', marginBottom: '20px' }}>
                  <Link to='/sujin'  className='link-item' onClick={handleClose}></Link>
                  <span className="ribbon">HOT</span>
                </div> */}
              {/* <br /> */}
              <p><Link to='/detalie' className='link-item' onClick={handleClose}></Link></p>
              <p><Link to='/banner' className='link-item' onClick={handleClose}> </Link></p>
              <p><Link to='/thum' className='link-item' onClick={handleClose}>    </Link></p>
              <hr></hr>
              <p><Link to='/made' className='link-item' onClick={handleClose}>  </Link></p>
              </div>          
            </Offcanvas.Body>
          </Offcanvas>
          </div>
        </Col>
      </Row>
    </Container>
    </>
  );
}

export default MainCanvas;